.main-nav {
  color: #000000;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
    top: auto;
    left: auto;
    width: auto;
    background: initial;
  }
}
.main-nav__wrapper {
  display: flex;
  justify-content: center;
}
