  .main-nav__wrapper {
    z-index: 1;
  }
}
.main-nav__toggle {
  width: 24px;
  height: 16px;
  position: absolute;
  top: -40px;
  left: 275px;
  cursor: pointer;
  border: 0;
  padding: 0;
  background: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  width: 24px;
  top: -40px;
  left: 275px;
  border: 2px;
  cursor: pointer;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 18px;
  background: url("/img/close_mobile.svg") no-repeat;
}
.main-nav--closed .main-nav__toggle {
  top: -40px;
  left: 275px;
  width: 24px;
  height: 2px;
  border: 2px;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background: url("/img/hide_mobile.svg") no-repeat;
}
.main-nav--closed .main-nav__wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
}
@media (min-width: 1220px) {
  .main-nav--closed .main-nav__wrapper {
    display: block;
    background-color: transparent;
    z-index: -1;
  }
}
.main-nav--opened .main-nav__wrapper {
  display: block;
  width: 320px;
}
